
import React, { useState } from 'react';
import { Drawer, Row, Col, Divider, Timeline} from 'antd';
import 'antd/dist/antd.css'

const DescriptionItem = ({ title, content }) => (
    <div className="site-description-item-profile-wrapper">
        <p className="site-description-item-profile-p-label">{title}: {content}</p>
    </div>
);

const UserProfile = (props) => {
    const [visible, setVisible] = useState(false);

    const showDrawer = () => {
        setVisible(true);
    };

    const onClose = () => {
        setVisible(false);
    };

    var {author, email} = props
    author = author !== undefined? author: '';
    email = email !== undefined? email: '1270563429@qq.com';

    return (
        <>
            <span onClick={showDrawer}></span>
            <Drawer width={640} title="" placement="right" onClose={onClose} visible={visible}>
                <p
                    className="site-description-item-profile-p"
                    style={{
                        marginBottom: 24,
                        fontWeight: "bolder",
                        fontSize: '1.3rem',
                        color: '#1890FF'
                    }}
                >
                </p>
                <Row>
                    <Col span={12}>
                        <DescriptionItem title="Full Name" content={author} />
                    </Col>
                    <Col span={12}>
                        <DescriptionItem title="Email" content={email} />
                    </Col>
                </Row>
                <Divider />
                <p
                    className="site-description-item-profile-p"
                    style={{
                        marginBottom: 24,
                        fontWeight: "bolder",
                        fontSize: '1.3rem',
                        color: '#1890FF'
                    }}
                >
                </p>
                <Row>
                    <Timeline mode='left'>
                        <Timeline.Item label="2022-06-03"></Timeline.Item>
                        <Timeline.Item label="2022-06-04">SwaggerSmart-docReact</Timeline.Item>
                        <Timeline.Item>ReactES6ant-design</Timeline.Item>
                        <Timeline.Item label="2022-06-05">markdownreact-markdownmd
                        remark-gfmhttps://github.com/remarkjs/react-markdown</Timeline.Item>
                    </Timeline>
                </Row>
            </Drawer>
        </>
    );
};


export default UserProfile;